import React, { ChangeEventHandler, useEffect, useState } from "react";
import { useGetLocale } from "providers";

import { useFetch } from "hooks";
import { Transition } from "components";

import { Template } from "pages/Auth/components";

import Form from "./Form";
import Loading from "./Loading";
import SuccessMessage from "./SuccessMessage";
import css from "./style.module.scss";

interface FetchData {
  success: boolean;
  message: string;
}

interface RequestNewPasswordProps {
  height: number;
}

const RequestNewPassword = ({ height }: RequestNewPasswordProps) => {
  const intl = useGetLocale();

  const [user, setUser] = useState("");
  const [fetch, { called, loading, data, error }] = useFetch<FetchData>(
    "account/request-new-password",
  );

  const handleChangeUser: ChangeEventHandler<HTMLInputElement> = (event) => {
    setUser(event.target.value);
  };

  const handleRequest = () => {
    fetch({
      variables: {
        user: user,
      },
    });
  };

  useEffect(() => {
    if (!called || loading) {
      return;
    }
  }, [called, loading, data, error]);

  return (
    <Template
      height={height}
      title={intl.formatMessage({ id: "form.requestNewPassword.title" })}
      description={
        data && data.success && data.message
          ? ""
          : intl.formatMessage({
              id: "form.requestNewPassword.description",
            })
      }>
      <div className={css.loadingWrapper}>
        <Transition in={loading} animation="fade">
          <Loading />
        </Transition>
      </div>
      <Transition in={!loading} animation="fade">
        {data && data.success && data.message ? (
          <SuccessMessage message={data.message} />
        ) : (
          <Form
            user={user}
            handleChangeUser={handleChangeUser}
            error={error}
            handleRequest={handleRequest}
          />
        )}
      </Transition>
    </Template>
  );
};

export default RequestNewPassword;
