import React from "react";
import { Popup } from "./Popup";
import { useFetch } from "hooks";
import { SocialButton } from "../../SocialButton";

interface Props {
  disabled: boolean;
  onAuthResponse: (data: any) => void;
  onErrorResponse: (message?: string) => void;
  onPopupOpen: () => void;
}

export const TwitterAuth = ({
  disabled,
  onAuthResponse,
  onErrorResponse,
  onPopupOpen,
}: Props) => {
  const [fetchLogin] = useFetch<any>("social/login");

  const onResponse = async (oauth_token: string, oauth_verifier: string) => {
    const response = await fetchLogin({
      variables: {
        social: "twitter",
        oauth_token: oauth_token,
        oauth_verifier: oauth_verifier,
        redirect_uri: window.location.origin,
      },
    });

    if (response.error) {
      onErrorResponse(response.error);
    } else {
      onAuthResponse(response.data);
    }
  };

  return (
    <Popup
      redirect_URL={window.location.origin}
      disabled={disabled}
      onResponse={onResponse}
      onError={onErrorResponse}
      onPopupOpen={onPopupOpen}>
      <SocialButton variant="twitter" disabled={disabled} />
    </Popup>
  );
};
