import React, { useState, useEffect } from "react";

import {
  GoogleAuth,
  TwitchAuth,
  TwitterAuth,
  MetamaskAuth,
  DiscordAuth,
  FacebookAuth,
} from "../SocialPopups";
import { SocialRegister } from "pages/Auth/tabs";
import { SocialHead } from "../SocialHead";

import { useFetch, useLocalStorage, useModalLocation } from "hooks";
import { notifyBarManager } from "components";

import css from "./style.module.scss";

type SocketToken = {
  message: string;
  success: boolean;
  socket_token: string;
};

export const SocialAuthContainer = () => {
  const [socketFetch] = useFetch<SocketToken>("account/get-socket-token");
  const [token, setToken] = useLocalStorage("token");
  const [, setExpirationTime] = useLocalStorage("expirationTime");

  const [isInProcess, setIsInProcess] = useState<boolean>(false);
  const [regToken, setRegToken] = useState<string>("");
  const [isRegModalOpel, setIsRegModalOpen] = useState<boolean>(false);

  const { close } = useModalLocation();

  const initSocketToken = async () => {
    socketFetch({
      variables: {
        token,
      },
    });
  };

  useEffect(() => {
    if (token) {
      initSocketToken();
    } else {
    }
  }, [token]);

  const onAuthResponse = (data: any) => {
    setIsInProcess(false);

    if (data.token) {
      onLogin(data);
    } else if (data.reg_token) {
      startRegister(data);
    }
  };

  const onLogin = (data: any, skipTFA: boolean = false) => {
    setToken(data.token);
    const oneHour = 60 * 60 * 1000;
    setExpirationTime(`${Date.now() + oneHour}`);

    if (data["2fa_required"] || skipTFA) {
      close();
    }
    closeRegModal();
  };

  const startRegister = (data: any) => {
    setRegToken(data.reg_token);
    setIsRegModalOpen(true);
  };

  const onError = (message?: string) => {
    setIsInProcess(false);
    if (message) {
      notifyBarManager.error({
        timeOut: 4000,
        message: message,
      });
    }
  };

  const onPopupOpen = () => {
    setIsInProcess(true);
  };

  const closeRegModal = () => {
    setIsRegModalOpen(false);
    setRegToken("");
  };

  return (
    <div>
      <SocialHead />
      <div className={css.container}>
        <GoogleAuth
          client_id="63461815524-2d38lkgvoht0m9nmcbv3p8usvk5u1ak3.apps.googleusercontent.com"
          disabled={isInProcess}
          onAuthResponse={onAuthResponse}
          onPopupOpen={onPopupOpen}
          onErrorResponse={onError}
        />

        <FacebookAuth
          client_id="676114457105344"
          disabled={isInProcess}
          onAuthResponse={onAuthResponse}
          onPopupOpen={onPopupOpen}
          onErrorResponse={onError}
        />
        <TwitchAuth
          client_id="m8kx9yui6vbq5tl78qq70ehvbpj6ml"
          disabled={isInProcess}
          onAuthResponse={onAuthResponse}
          onPopupOpen={onPopupOpen}
          onErrorResponse={onError}
        />
        <DiscordAuth
          client_id="918077356680302642"
          disabled={isInProcess}
          onAuthResponse={onAuthResponse}
          onPopupOpen={onPopupOpen}
          onErrorResponse={onError}
        />
        <TwitterAuth
          disabled={isInProcess}
          onAuthResponse={onAuthResponse}
          onPopupOpen={onPopupOpen}
          onErrorResponse={onError}
        />
        <MetamaskAuth
          disabled={isInProcess}
          onAuthResponse={onAuthResponse}
          onPopupOpen={onPopupOpen}
          onErrorResponse={onError}
        />
      </div>
      {!!regToken && isRegModalOpel ? (
        <SocialRegister
          reg_token={regToken}
          onClose={closeRegModal}
          onLogin={onLogin}
        />
      ) : null}
    </div>
  );
};
