import React, {
  useState,
  useEffect,
  useCallback,
  MouseEventHandler,
} from "react";
import { Link } from "react-router-dom";
import { useGetLocale } from "providers";
import { useFetch, useLocalStorage } from "hooks";
import {
  validateUsername,
  maximumUserNameLength,
} from "../Register/validateFields";
import { Input, InputProps } from "pages/ModalPages/componentsProgressive";
import { FormHelperText, Icon } from "components";
import Checkbox, { Props as CheckboxProps } from "components/Checkbox";

import { SubmitButton } from "pages/Auth/components";
import css from "./style.module.scss";
import { generateName } from "./userNameGenerator";

interface Props {
  reg_token: string;
  onClose: () => void;
  onLogin: (data: any, skipTFA: boolean) => void;
}

const SocialRegister = ({ reg_token, onClose, onLogin }: Props) => {
  const intl = useGetLocale();

  const [fetch, { called, loading, data, error }] =
    useFetch<any>("social/register");

  const [userName, setUserName] = useState<string>("");
  const [isAgree, setIsAgree] = useState<boolean>(false);
  const [userNameError, setUserNameError] = useState<string>("");

  const [refer] = useLocalStorage("refer");
  const [sourceRefer] = useLocalStorage("sourceRefer");

  const handleChangeUserName: InputProps["onChange"] = (event) => {
    setUserNameError("");
    setUserName(event.target.value);
  };

  const handleChangeTerms: CheckboxProps["onChange"] = useCallback((event) => {
    setIsAgree(event.target.checked);
  }, []);

  const onCloseClick = (event: any) => {
    event.preventDefault();
    onClose();
  };

  const handleRegister = () => {
    const error_message = validateUsername(userName, intl.formatMessage);
    if (error_message) {
      setUserNameError(error_message);
      return;
    }

    const referrer = refer ? `${refer}` : "";
    const source = sourceRefer ? `${sourceRefer}` : "";

    const variables = {
      user_name: userName,
      agreement: 1,
      reg_token: reg_token,
      referrer,
      source,
    };

    fetch({ variables });
  };

  useEffect(() => {
    if (!called || loading || !data || error) {
      return;
    }
    onLogin(data, true);
  }, [called, loading, data, error]);

  useEffect(() => {
    setUserName(generateName(maximumUserNameLength));
  }, []);

  return (
    <div className={css.root}>
      <div className={css.container}>
        <div className={css.header_container}>
          <span className={css.header}>
            {intl.formatMessage({ id: "register" })}
          </span>
          <Icon variant="close" fontSize={16} onClick={onCloseClick} />
        </div>
        <Input
          id="register-userName-input"
          name="user_name"
          label={intl.formatMessage({ id: "form.usernameLabel" })}
          value={userName}
          error={!!userNameError}
          helperText={userNameError}
          onChange={handleChangeUserName}
        />
        <Checkbox
          id="register-terms-of-use"
          checked={isAgree}
          onChange={handleChangeTerms}
          classes={{
            root: css.checkboxWrapper,
            checkbox: css.checkbox,
            checked: css.checkboxChecked,
            icon: css.checkboxIcon,
          }}
          label={
            <div className={css.checkboxLabelWrapper}>
              {intl.formatMessage(
                { id: "form.register.agreement" },
                {
                  PPLink: (
                    <Link className={css.link} to={{ pathname: "/privacy" }}>
                      {intl.formatMessage({
                        id: "form.register.privacyPolicy",
                      })}
                    </Link>
                  ),
                  TOULink: (
                    <Link className={css.link} to={{ pathname: "/terms" }}>
                      {intl.formatMessage({ id: "form.register.termsOfUse" })}
                    </Link>
                  ),
                },
              )}
            </div>
          }
        />

        {error && <FormHelperText error>{error}</FormHelperText>}

        <SubmitButton
          disabled={!isAgree}
          onClick={handleRegister}
          loading={loading}>
          {intl.formatMessage({ id: "form.register.button" })}
        </SubmitButton>
      </div>
    </div>
  );
};

export default SocialRegister;
