import React, { MouseEvent, FC } from "react";
import { track } from "utils";

interface Props {
  client_id: string;
  redirect_URL: string;
  disabled: boolean;
  onResponse: (access_token: string, id_token: string) => void;
  onError: (message?: string) => void;
  onPopupOpen: () => void;
}

export const Popup: FC<Props> = ({
  client_id,
  redirect_URL,
  disabled,
  onResponse,
  onError,
  onPopupOpen,
  children,
}) => {
  const onButtonClick = (e: MouseEvent<HTMLDivElement>) => {
    e.preventDefault();

    if (disabled) {
      return;
    }

    onPopupOpen();
    getUserCode();
  };

  const getUserCode = () => {
    try {
      const popup = openPopup();
      const authURL = `https://accounts.google.com/o/oauth2/auth?client_id=${client_id}&redirect_uri=${redirect_URL}&response_type=id_token token&scope=https://www.googleapis.com/auth/userinfo.email https://www.googleapis.com/auth/userinfo.profile`;
      popup!.location.href = authURL;
      popup?.focus();
      polling(popup);
    } catch (error) {
      track()?.errors(error);
    }
  };

  const polling = (popup: Window | null) => {
    const pollingInterval = setInterval(() => {
      if (!popup || popup.closed || popup.closed === undefined) {
        clearInterval(pollingInterval);
        onError();
        return;
      }
      const closeDialog = () => {
        clearInterval(pollingInterval);
        popup.close();
      };

      try {
        if (
          !popup.location.hostname.includes("accounts.google.com") &&
          !popup.location.href.includes("about:blank")
        ) {
          const raw = popup.location.href.split("#")[1];
          const params = raw.split("&");
          let access_token;
          let id_token;

          for (let i = 0; i < params.length; i++) {
            const paramData = params[i].split("=");
            if (paramData[0] === "access_token") {
              access_token = paramData[1];
            }
            if (paramData[0] === "id_token") {
              id_token = paramData[1];
            }
          }
          if (access_token && id_token) {
            onResponse(access_token, id_token);
          } else {
            onError("Google auth error");
          }
          closeDialog();
          return;
        }
      } catch (error) {
        track()?.errors(error);
      }
    }, 500);
  };

  const openPopup = () => {
    return window.open(
      "",
      "",
      "toolbar=no, location=no, directories=no status=no, menubar=no, scrollbars=no, copyhistory=no, width=600, height=400, top=200, left=20",
    );
  };

  return <div onClick={onButtonClick}>{children}</div>;
};
