import React, { useMemo, useState } from "react";
import SwipeableViews from "react-swipeable-views";

import { ProgressiveModal } from "components";
import { ModalNames, ModalsProps } from "config/routes";

import { Login, Register, RequestNewPassword } from "./tabs";
import css from "./style.module.scss";

const tabsHardcodedHeight = [620, 620, 346];

const containerStyle = {
  overflow: "hidden",
  "overflow-y": "auto",
};

const AuthModal = ({ isOpen, modalName }: ModalsProps) => {
  const [selectedIndex, setSelectedIndex] = useState(-1);
  const { height, selectedIndexValue } = useMemo(() => {
    let selectedIndexValue = selectedIndex;
    switch (modalName) {
      case "login":
        selectedIndexValue = 0;
        break;
      case "register":
        selectedIndexValue = 1;
        break;
      case "passwordRecovery":
        selectedIndexValue = 2;
        break;
      default:
        break;
    }

    return {
      height: tabsHardcodedHeight[selectedIndexValue] || 0,
      selectedIndexValue,
    };
  }, [modalName]);

  if (isOpen && selectedIndexValue !== selectedIndex) {
    setSelectedIndex(selectedIndexValue);
  }

  return (
    <ProgressiveModal
      open={isOpen}
      height={height}
      className={css.paper}
      width={381}
      aria-labelledby="auth-dialog-title"
      aria-describedby="auth-dialog-description">
      <SwipeableViews
        slideStyle={containerStyle}
        disabled
        disableLazyLoading
        index={selectedIndex}>
        <Login height={height} />
        <Register height={height} />
        <RequestNewPassword height={height} />
      </SwipeableViews>
    </ProgressiveModal>
  );
};

export default AuthModal;
