import React from "react";

import { useGetLocale } from "providers";
import { useFetch } from "hooks";

import { SocialButton } from "../../SocialButton";
import { track } from "utils";

interface Props {
  disabled: boolean;
  onAuthResponse: (data: any) => void;
  onErrorResponse: (message?: string) => void;
  onPopupOpen: () => void;
}

export const MetamaskAuth = ({
  disabled,
  onAuthResponse,
  onErrorResponse,
  onPopupOpen,
}: Props) => {
  const intl = useGetLocale();
  const [fetchNonce] = useFetch<any>("social/metamask-nonce");
  const [fetchLogin] = useFetch<any>("social/login");

  const handleClick = async (e: any) => {
    e.preventDefault();

    if (disabled) {
      return;
    }

    onPopupOpen();
    //@ts-ignore
    if (typeof window.ethereum === "undefined") {
      onErrorResponse(
        intl.formatMessage({
          id: "feyorraHouseEdge.errors.metaMaskNotInstall",
        }),
      );
      return;
    }

    try {
      //@ts-ignore
      await window.ethereum.request({
        method: "eth_requestAccounts",
      });
    } catch (err) {
      track()?.errors(err);
      return;
    }

    let signature;
    let address;
    try {
      const ethers = await import("@ethersproject/providers");
      const { Web3Provider } = ethers;

      const provider = new Web3Provider(window.ethereum);
      const signer = provider.getSigner();
      address = await signer.getAddress();
      const nonceData = await fetchNonce({
        variables: {
          address: address,
        },
      });
      if (nonceData.error || !nonceData.data) {
        console.log(nonceData.error);
        onErrorResponse(nonceData.error);
        return;
      }
      signature = await signer.signMessage(nonceData.data.nonce);
    } catch (error: any) {
      track()?.errors(error);

      if (error.code == 4001) {
        onErrorResponse();
      } else {
        onErrorResponse(error.message);
      }

      return;
    }

    const response = await fetchLogin({
      variables: {
        social: "metamask",
        signature: signature,
        address: address,
      },
    });

    if (response.error) {
      onErrorResponse(response.error);
    } else {
      onAuthResponse(response.data);
    }
  };

  return (
    <div onClick={handleClick}>
      <SocialButton variant="metamask" disabled={disabled} />
    </div>
  );
};
