import React, { MouseEvent, FC } from "react";
import { track } from "utils";

interface Props {
  client_id: string;
  redirect_URL: string;
  disabled: boolean;
  onResponse: (code: string) => void;
  onError: (message?: string) => void;
  onPopupOpen: () => void;
}

export const Popup: FC<Props> = ({
  client_id,
  redirect_URL,
  disabled,
  onResponse,
  onError,
  onPopupOpen,
  children,
}) => {
  const onButtonClick = (e: MouseEvent<HTMLDivElement>) => {
    e.preventDefault();

    if (disabled) {
      return;
    }

    onPopupOpen();
    getUserCode();
  };

  const getUserCode = () => {
    const popup = openPopup();
    const authURL = `https://discord.com/api/oauth2/authorize?response_type=code&client_id=${client_id}&scope=identify%20email&redirect_uri=${redirect_URL}&prompt=none`;
    popup!.location.href = authURL;
    polling(popup);
  };

  const polling = (popup: Window | null) => {
    const pollingInterval = setInterval(() => {
      if (!popup || popup.closed || popup.closed === undefined) {
        clearInterval(pollingInterval);
        onError();
        return;
      }
      const closeDialog = () => {
        clearInterval(pollingInterval);
        popup.close();
      };

      try {
        if (!popup.location.hostname.includes("discord.com")) {
          if (popup.location.search) {
            const query = new URLSearchParams(popup.location.search);

            const code = query.get("code");

            if (code) {
              onResponse(code);
            } else {
              onError("Discord auth error");
            }
            closeDialog();
            return;
          }
        }
      } catch (error) {
        track()?.errors(error);
      }
    }, 500);
  };

  const openPopup = () => {
    return window.open(
      "",
      "",
      "toolbar=no, location=no, directories=no status=no, menubar=no, scrollbars=no, copyhistory=no, width=600, height=400, top=200, left=20",
    );
  };

  return <div onClick={onButtonClick}>{children}</div>;
};
