import React from "react";
import { Link } from "react-router-dom";
import { useGetLocale } from "providers";
import ReCAPTCHA from "react-google-recaptcha";

import Checkbox from "components/Checkbox";

import { Input } from "pages/ModalPages/componentsProgressive";
import { SubmitButton, Template } from "pages/Auth/components";
import { ReactComponent as Arrow } from "assets/icons/chevron-left.svg";

import { ErrorsInterface } from "../../types";
import css from "../../style.module.scss";

interface Props {
  height: number;
  errors: ErrorsInterface;
  userName: string;
  handleChangeUserName: React.ChangeEventHandler<HTMLInputElement>;
  checked: boolean;
  onCheckboxChange:
    | ((event: React.ChangeEvent<HTMLInputElement>, checked: boolean) => void)
    | undefined;
  reCaptchaRef: React.RefObject<ReCAPTCHA>;
  onReCAPTCHAChange: ((token: string | null) => void) | undefined;
  loading: boolean;
  onGoBackButtonClick: () => void;
  onButtonClick: () => void;
  buttonDisabled: boolean;
}

export const SecondRegisterStep = ({
  height,
  errors,
  userName,
  handleChangeUserName,
  checked,
  onCheckboxChange,
  onGoBackButtonClick,
  onButtonClick,
  reCaptchaRef,
  onReCAPTCHAChange,
  loading,
  buttonDisabled,
}: Props) => {
  const intl = useGetLocale();
  return (
    <div className={css.stepContainer}>
      <Arrow className={css.arrow} onClick={onGoBackButtonClick} />

      <Template height={height} title={intl.formatMessage({ id: "register" })}>
        <Input
          id="register-userName-input"
          name="user_name"
          label={intl.formatMessage({ id: "form.usernameLabel" })}
          value={userName}
          error={!!errors.userName}
          helperText={errors.userName}
          onChange={handleChangeUserName}
        />

        <Checkbox
          id="register-terms-of-use"
          checked={checked}
          onChange={onCheckboxChange}
          classes={{
            root: css.checkboxWrapper,
            checkbox: css.checkbox,
            checked: css.checkboxChecked,
            icon: css.checkboxIcon,
          }}
          label={
            <div className={css.checkboxLabelWrapper}>
              {intl.formatMessage(
                { id: "form.register.agreement" },
                {
                  PPLink: (
                    <Link className={css.link} to={{ pathname: "/privacy" }}>
                      {intl.formatMessage({
                        id: "form.register.privacyPolicy",
                      })}
                    </Link>
                  ),
                  TOULink: (
                    <Link className={css.link} to={{ pathname: "/terms" }}>
                      {intl.formatMessage({ id: "form.register.termsOfUse" })}
                    </Link>
                  ),
                },
              )}
            </div>
          }
        />
        <div className={css.reCaptchaWrapper}>
          <ReCAPTCHA
            size="normal"
            sitekey={process.env.REACT_APP_RECAPTCHA_SITE_KEY as string}
            onChange={onReCAPTCHAChange}
            ref={reCaptchaRef}
          />
        </div>
        <SubmitButton
          disabled={buttonDisabled}
          onClick={onButtonClick}
          loading={loading}>
          {intl.formatMessage({ id: "form.register.button" })}
        </SubmitButton>
      </Template>
    </div>
  );
};
