import React, { ChangeEventHandler } from "react";
import { useGetLocale } from "providers";

import { Input } from "pages/ModalPages/componentsProgressive";
import { SubmitButton } from "pages/Auth/components";

import css from "./style.module.scss";
import { FormHelperText } from "components";

interface FormProps {
  user: string;
  handleChangeUser: ChangeEventHandler<HTMLInputElement>;
  error: any;
  handleRequest: () => void;
}

const Form = ({ user, handleChangeUser, error, handleRequest }: FormProps) => {
  const intl = useGetLocale();

  return (
    <>
      <Input
        id="request-new-password-input"
        label={intl.formatMessage({ id: "form.requestNewPassword.input" })}
        value={user}
        onChange={handleChangeUser}
      />

      {error && (
        <div className={css.errorWrapper}>
          <FormHelperText error>{error}</FormHelperText>
        </div>
      )}

      <SubmitButton disabled={!user} onClick={handleRequest}>
        {intl.formatMessage({ id: "form.requestNewPassword.button" })}
      </SubmitButton>
    </>
  );
};

export default Form;
