import React, { useCallback, useEffect, useRef, useState } from "react";
import { useGetLocale } from "providers";
import ReCAPTCHA, { ReCAPTCHAProps } from "react-google-recaptcha";
import clsx from "clsx";

import notifybarManager from "components/NotifyBar/notifybar-manager";

import { LinkWithSearch, Typography } from "components";
import { useFetch, useLocalStorage, useModalLocation } from "hooks";

import { SubmitButton, Template } from "pages/Auth/components";
import { Input, InputProps } from "pages/ModalPages/componentsProgressive";
import LoginButton from "components/LoginButton";
import { SocialAuthContainer } from "./components/SocialAuthContainer";

import css from "./style.module.scss";

interface FetchData {
  success: boolean;
  token?: string;
  "2fa_required"?: string;
  message: string;
}

type SocketToken = {
  message: string;
  success: boolean;
  socket_token: string;
};

interface LoginProps {
  height: number;
}

const Login = ({ height }: LoginProps) => {
  const intl = useGetLocale();

  const [token, setToken] = useLocalStorage("token");
  const [, setExpirationTime] = useLocalStorage("expirationTime");

  const [user, setUser] = useState("");
  const [password, setPassword] = useState("");
  const [reResponse, setReResponse] = useState<string | null>(null);

  const [fetch, { called, loading, data, error }] =
    useFetch<FetchData>("account/login");
  const [socketFetch] = useFetch<SocketToken>("account/get-socket-token");

  const reCaptchaRef = useRef<ReCAPTCHA>(null);

  const handleChangeUser: InputProps["onChange"] = (event) => {
    setUser(event.target.value);
  };

  const handleChangePassword: InputProps["onChange"] = (event) => {
    setPassword(event.target.value);
  };

  const { close } = useModalLocation();

  const handleLogin = () => {
    fetch({
      variables: {
        user: user,
        password: password,
        re_response: reResponse,
      },
    });
  };

  const initSocketToken = async () => {
    socketFetch({
      variables: {
        token,
      },
    });
  };

  useEffect(() => {
    if (token) {
      initSocketToken();
    } else {
    }
  }, [token]);

  useEffect(() => {
    if (error) {
      setReResponse(null);
      reCaptchaRef.current?.reset();
    }

    if (!called || loading || !data?.token) {
      return;
    }

    setToken(data.token);
    const oneHour = 60 * 60 * 1000;
    setExpirationTime(`${Date.now() + oneHour}`);

    if (data["2fa_required"]) {
      close();
    }
  }, [called, loading, data, error]);

  useEffect(() => {
    if (error) {
      notifybarManager.error({
        message: error,
        timeOut: 8000,
      });
    }
  }, [error]);

  const onReCAPTCHAChange: ReCAPTCHAProps["onChange"] = useCallback((value) => {
    setReResponse(value);
  }, []);

  return (
    <Template
      height={height}
      title={intl.formatMessage({ id: "form.login.title" })}>
      <LoginButton
        active="login"
        style={{
          marginTop: 10,
          width: "100%",
          height: 45,
        }}
        withBorder
      />

      <Input
        id="login-email-input"
        label={intl.formatMessage({ id: "form.emailLabel" })}
        value={user}
        onChange={handleChangeUser}
        error={error}
      />

      <Input
        error={error}
        id="login-password-input"
        type="password"
        label={intl.formatMessage({ id: "form.passwordLabel" })}
        value={password}
        onChange={handleChangePassword}
      />

      <div className={css.reCaptchaWrapper}>
        <ReCAPTCHA
          sitekey={process.env.REACT_APP_RECAPTCHA_SITE_KEY as string}
          onChange={onReCAPTCHAChange}
          ref={reCaptchaRef}
        />
      </div>

      <SubmitButton
        onClick={handleLogin}
        loading={loading}
        disabled={!reResponse || !user || !password}>
        {intl.formatMessage({ id: "form.login.button" })}
      </SubmitButton>

      <div className={css.linkWrapper}>
        <Typography className={css.recoverText}>
          {intl.formatMessage(
            { id: "form.login.forgotPassword" },
            {
              recover: (
                <LinkWithSearch
                  className={clsx(css.recoverText, css.link)}
                  search={{ name: "page", value: "passwordRecovery" }}>
                  {intl.formatMessage({ id: "form.login.recover" })}
                </LinkWithSearch>
              ),
            },
          )}
        </Typography>
      </div>
      <SocialAuthContainer />
    </Template>
  );
};

export default React.memo(Login);
