import React from "react";
import google from "./google.png";
import facebook from "./facebook.png";
import discord from "./discord.png";
import twitch from "./twitch.png";
import twitter from "./twitter.png";
import metamask from "./metamask.png";

import css from "./styles.module.scss";
import clsx from "clsx";
type variantType =
  | "google"
  | "facebook"
  | "discord"
  | "metamask"
  | "twitch"
  | "twitter";

interface Props {
  variant: variantType;
  disabled: boolean;
}
export const SocialButton = ({ variant, disabled }: Props) => {
  const getImage = (variant: variantType) => {
    switch (variant) {
      case "google":
        return google;
      case "discord":
        return discord;
      case "facebook":
        return facebook;
      case "metamask":
        return metamask;
      case "twitch":
        return twitch;
      case "twitter":
        return twitter;
    }
  };

  return (
    <div className={clsx(css.root, { [css.disabled]: disabled })}>
      <img className={css.img} src={getImage(variant)} />
    </div>
  );
};
