import React from "react";
import css from "./style.module.scss";
import { useGetLocale } from "providers";

export const SocialHead = () => {
  const intl = useGetLocale();
  return (
    <div className={css.root}>
      <div className={css.start_line}></div>
      <div className={css.text}>
        {intl.formatMessage({
          id: "socialMessages.orContinueWith",
        })}
      </div>
      <div className={css.end_line}></div>
    </div>
  );
};
