import React, { MouseEvent, FC } from "react";
import { useFetch } from "hooks";
import { track } from "utils";

interface Props {
  redirect_URL: string;
  disabled: boolean;
  onResponse: (oauth_token: string, oauth_verifier: string) => void;
  onError: (message?: string) => void;
  onPopupOpen: () => void;
}

export const Popup: FC<Props> = ({
  redirect_URL,
  disabled,
  onResponse,
  onError,
  onPopupOpen,
  children,
}) => {
  const [fetchToken] = useFetch<any>("social/twitter-token");

  const onButtonClick = (e: MouseEvent<HTMLDivElement>) => {
    e.preventDefault();

    if (disabled) {
      return;
    }

    onPopupOpen();
    getUserCode();
  };

  const getUserCode = async () => {
    const tokenResponse = await fetchToken({
      variables: {
        redirect_url: redirect_URL,
      },
    });
    if (tokenResponse.error || !tokenResponse.data) {
      onError(tokenResponse.error.message);
      return;
    }

    const popup = openPopup();
    let authURL = `https://api.twitter.com/oauth/authenticate?oauth_token=${tokenResponse.data.oauth_token}&force_login=false`;

    popup!.location.href = authURL;
    polling(popup);
  };

  const polling = (popup: Window | null) => {
    const pollingInterval = setInterval(() => {
      if (!popup || popup.closed || popup.closed === undefined) {
        clearInterval(pollingInterval);
        onError();
        return;
      }
      const closeDialog = () => {
        clearInterval(pollingInterval);
        popup.close();
      };

      try {
        if (
          !popup.location.hostname.includes("api.twitter.com") &&
          //@ts-ignore
          !popup.location.hostname == ""
        ) {
          if (popup.location.search) {
            const query = new URLSearchParams(popup.location.search);
            const oauth_token = query.get("oauth_token");
            const oauth_verifier = query.get("oauth_verifier");

            if (oauth_token && oauth_verifier) {
              onResponse(oauth_token, oauth_verifier);
            } else {
              onError("Twitter auth error");
            }
            closeDialog();
          } else {
            onError("Twitter auth error");
            closeDialog();
          }
        }
      } catch (error) {
        track()?.errors(error);
      }
    }, 500);
  };

  const openPopup = () => {
    return window.open(
      "",
      "",
      "toolbar=no, location=no, directories=no status=no, menubar=no, scrollbars=no, copyhistory=no, width=600, height=400, top=200, left=20",
    );
  };

  return <div onClick={onButtonClick}>{children}</div>;
};
