export const generateName = (max: number): string => {
  const part1 = getRandomPart();
  const part2 = getRandomPart();

  let currentName = part1 + part2;
  if (currentName.length >= max) {
    currentName = part1.length < part2.length ? part1 : part2;
  }

  currentName += getPartMaxLength(max - 2 - currentName.length);

  currentName += getRandomNumbers(max - currentName.length);
  return currentName;
};

const getRandomPart = (): string => {
  return nameList[Math.floor(Math.random() * nameList.length)];
};

const getPartMaxLength = (max: number) => {
  if (max < 1) {
    return "";
  }
  const arr: string[] = [];

  for (let i = 0; i < nameList.length; i++) {
    if (nameList[i].length <= max) {
      arr.push(nameList[i]);
    }
  }
  if (arr.length > 0) {
    return arr[Math.floor(Math.random() * arr.length)];
  } else {
    return "";
  }
};

const getRandomNumbers = (max: number): string => {
  return Math.random()
    .toString()
    .substring(2, 2 + max);
};
