import React, { FC } from "react";
import { Popup } from "./Popup";
import { useFetch } from "hooks";
import { SocialButton } from "../../SocialButton";
import { fetchBase } from "utils";

interface Props {
  client_id: string;
  disabled: boolean;
  onAuthResponse: (data: any) => void;
  onErrorResponse: (message?: string) => void;
  onPopupOpen: () => void;
}

export const GoogleAuth = ({
  client_id,
  disabled,
  onAuthResponse,
  onErrorResponse,
  onPopupOpen,
}: Props) => {
  const onResponse = async (access_token: string, id_token: string) => {
    const response = await fetchBase("social/login", {
      variables: {
        social: "google",
        access_token: access_token,
        id_token: id_token,
      },
    });

    if (response.error) {
      onErrorResponse(response.error);
    } else {
      onAuthResponse(response.data);
    }
  };

  return (
    <Popup
      client_id={client_id}
      redirect_URL={window.location.origin}
      disabled={disabled}
      onResponse={onResponse}
      onError={onErrorResponse}
      onPopupOpen={onPopupOpen}>
      <SocialButton variant="google" disabled={disabled} />
    </Popup>
  );
};
