import React, {
  useCallback,
  useEffect,
  useRef,
  useState,
  useMemo,
} from "react";
import { useGetLocale } from "providers";
import { useHistory, useLocation } from "react-router-dom";
import ReCAPTCHA, { ReCAPTCHAProps } from "react-google-recaptcha";
import SwipeableViews from "react-swipeable-views";

import { useFetch, useLocalStorage, useModalLocation } from "hooks";
import { notifyBarManager } from "components";
import { Props as CheckboxProps } from "components/Checkbox";

import { InputProps } from "pages/ModalPages/componentsProgressive";
import { FirstRegisterStep } from "./components/FirstRegisterStep";
import { SecondRegisterStep } from "./components/SecondRegisterStep";

import validateFields from "./validateFields";

interface FetchData {
  success: boolean;
  message: string;
}
interface RegisterProps {
  height: number;
}

const Register = ({ height }: RegisterProps) => {
  const intl = useGetLocale();
  const location = useLocation();
  const history = useHistory();

  const [userName, setUserName] = useState("");
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");

  const [isAgree, setIsAgree] = useState(false);
  const [reResponse, setReResponse] = useState<string | null>(null);
  const [activeIndex, setActiveIndex] = useState(0);

  const [errors, setErrors] = useState({
    userName: "",
    email: "",
    password: "",
  });

  const [refer] = useLocalStorage("refer");
  const [sourceRefer] = useLocalStorage("sourceRefer");
  const utmSource = localStorage.getItem("utm_source");
  const { close } = useModalLocation();

  const [fetch, { called, loading, data, error }] =
    useFetch<FetchData>("account/register/");

  const reCaptchaRef = useRef<ReCAPTCHA>(null);

  const isRegisterUnable = useMemo(() => {
    return !isAgree || !reResponse;
  }, [isAgree, reResponse]);

  const handleChangeUserName: InputProps["onChange"] = (event) => {
    setErrors({
      ...errors,
      userName: "",
    });
    setUserName(event.target.value);
  };

  const handleChangeEmail: InputProps["onChange"] = (event) => {
    setErrors({
      ...errors,
      email: "",
    });
    setEmail(event.target.value);
  };

  const handleChangePassword: InputProps["onChange"] = (event) => {
    setErrors({
      ...errors,
      password: "",
    });
    setPassword(event.target.value);
  };

  const handleRegister = () => {
    if (
      !validateFields(
        { userName, email, password },
        setErrors,
        intl.formatMessage,
      )
    ) {
      return;
    }

    const referrer = refer ? `${refer}` : "";
    const source = sourceRefer ? `${sourceRefer}` : "";

    const variables = {
      user_name: userName,
      user_email: email,
      password: password,
      re_response: reResponse,
      agreement: 1,
      referrer,
      source,
      utm_source: utmSource,
    };

    fetch({ variables });
  };

  const handleChangeTerms: CheckboxProps["onChange"] = useCallback((event) => {
    setIsAgree(event.target.checked);
  }, []);

  const onReCAPTCHAChange: ReCAPTCHAProps["onChange"] = useCallback((value) => {
    setReResponse(value);
    setErrors({
      userName: "",
      email: "",
      password: "",
    });
  }, []);

  useEffect(() => {
    if (error) {
      setReResponse(null);
      if (reCaptchaRef.current) {
        reCaptchaRef.current.reset();
      }
    }

    if (!called || loading || !data) {
      return;
    }

    history.push({ ...location, search: "" });
    close();
  }, [called, loading, data, error]);

  useEffect(() => {
    const { email, password, userName } = errors;

    if (email || password || userName) {
      setReResponse(null);
      if (reCaptchaRef.current) {
        reCaptchaRef.current.reset();
      }
    }
  }, [errors]);

  useEffect(() => {
    if (errors.email) {
      setTimeout(() => {
        notifyBarManager.error({
          message: errors.email,
          timeOut: 8000,
        });
      }, 0);
    }
  }, [errors.email]);
  useEffect(() => {
    if (errors.password) {
      setTimeout(() => {
        notifyBarManager.error({
          message: errors.password,
          timeOut: 8000,
        });
      }, 0);
    }
  }, [errors.password]);
  useEffect(() => {
    if (errors.userName) {
      setTimeout(() => {
        notifyBarManager.error({
          message: errors.userName,
          timeOut: 8000,
        });
      }, 0);
    }
  }, [errors.userName]);

  useEffect(() => {
    if (error) {
      notifyBarManager.error({
        message: error,
        timeOut: 8000,
      });
    }
  }, [error]);

  return (
    <SwipeableViews disabled index={activeIndex} animateTransitions={false}>
      <FirstRegisterStep
        height={height}
        errors={errors}
        email={email}
        handleChangeEmail={handleChangeEmail}
        password={password}
        handleChangePassword={handleChangePassword}
        loading={loading}
        onButtonClick={() => setActiveIndex(1)}
        buttonDisabled={email === "" || password === ""}
      />

      <SecondRegisterStep
        height={height}
        errors={errors}
        userName={userName}
        handleChangeUserName={handleChangeUserName}
        checked={isAgree}
        onCheckboxChange={handleChangeTerms}
        onGoBackButtonClick={() => setActiveIndex(0)}
        onButtonClick={handleRegister}
        reCaptchaRef={reCaptchaRef}
        onReCAPTCHAChange={onReCAPTCHAChange}
        loading={loading}
        buttonDisabled={isRegisterUnable}
      />
    </SwipeableViews>
  );
};

export default React.memo(Register);
