import React from "react";
import { Popup } from "./Popup";
import { useFetch } from "hooks";
import { SocialButton } from "../../SocialButton";

interface Props {
  client_id: string;
  disabled: boolean;
  onAuthResponse: (data: any) => void;
  onErrorResponse: (message?: string) => void;
  onPopupOpen: () => void;
}

export const DiscordAuth = ({
  client_id,
  disabled,
  onAuthResponse,
  onErrorResponse,
  onPopupOpen,
}: Props) => {
  const [fetchLogin] = useFetch<any>("social/login");

  const onResponse = async (code: string) => {
    const response = await fetchLogin({
      variables: {
        social: "discord",
        code: code,
        redirect_uri: window.location.origin,
      },
    });

    if (response.error) {
      onErrorResponse(response.error);
    } else {
      onAuthResponse(response.data);
    }
  };

  return (
    <Popup
      client_id={client_id}
      redirect_URL={window.location.origin}
      disabled={disabled}
      onResponse={onResponse}
      onError={onErrorResponse}
      onPopupOpen={onPopupOpen}>
      <SocialButton variant="discord" disabled={disabled} />
    </Popup>
  );
};
