import React from "react";
import { useGetLocale } from "providers";

import { LoginButton } from "components";
import { Input } from "pages/ModalPages/componentsProgressive";
import { SubmitButton as AuthButton, Template } from "pages/Auth/components";
import { SocialAuthContainer } from "../../../Login/components/SocialAuthContainer";

import { ErrorsInterface } from "../../types";
import css from "../../style.module.scss";

interface Props {
  height: number;
  errors: ErrorsInterface;
  email: string;
  handleChangeEmail: React.ChangeEventHandler<HTMLInputElement>;
  password: string;
  handleChangePassword: React.ChangeEventHandler<HTMLInputElement>;
  loading: boolean;
  onButtonClick: () => void;
  buttonDisabled: boolean;
}

export const FirstRegisterStep = ({
  height,
  errors,
  email,
  handleChangeEmail,
  password,
  handleChangePassword,
  onButtonClick,
  loading,
  buttonDisabled,
}: Props) => {
  const intl = useGetLocale();
  return (
    <div className={css.stepContainer}>
      <Template
        height={height}
        title={intl.formatMessage({ id: "form.register.title" })}>
        <LoginButton
          style={{ marginTop: 10, width: "100%", height: 45 }}
          withBorder
        />
        <Input
          id="register-email-input"
          name="user_email"
          label={intl.formatMessage({ id: "form.emailLabel" })}
          value={email}
          error={!!errors.email}
          helperText={errors.email}
          onChange={handleChangeEmail}
        />

        <Input
          id="register-password-input"
          type="password"
          name="password"
          label={intl.formatMessage({ id: "form.passwordLabel" })}
          value={password}
          error={!!errors.password}
          helperText={errors.password}
          onChange={handleChangePassword}
        />

        <AuthButton
          type="button"
          onClick={onButtonClick}
          loading={loading}
          disabled={buttonDisabled}>
          {intl.formatMessage({ id: "form.register.button" })}
        </AuthButton>

        <SocialAuthContainer />
      </Template>
    </div>
  );
};
